import warzone from './map-overview-bg.jpg';

import { useCanvas } from './useCanvas';

import { ContextMenu, Item, Separator, Submenu, ContextMenuProvider } from 'react-contexify';
import 'react-contexify/dist/ReactContexify.min.css';

import './App.css';
import { useState } from 'react';

const onClick = ({ event, ref, data, dataFromProvider }) => console.log('Hello');
// create your menu first
const MyAwesomeMenu = () => (
    <ContextMenu id='menu_id'>
       <Item onClick={onClick}>Lorem</Item>
       <Item onClick={onClick}>Ipsum</Item>
       <Separator />
       <Item disabled>Dolor</Item>
       <Separator />
       <Submenu label="Foobar">
        <Item onClick={onClick}>Foo</Item>
        <Item onClick={onClick}>Bar</Item>
       </Submenu>
    </ContextMenu>
);


function App() {
  const [showContextMenu, setShowContextMenu] = useState(false);
  const [ coordinates, setCoordinates, canvasRef, canvasWidth, canvasHeight ] = useCanvas();

  const handleCanvasClick=(event)=>{
    console.log(event)
    if (event.type === 'click') {
      console.log('Left click');
    } else if (event.type === 'contextmenu') {
      console.log('Right click');
      // setShowContextMenu(true)
    }

    // on each click get current mouse location 
    const currentCoord = { x: event.clientX, y: event.clientY };
      // add the newest mouse location to an array in state 
      setCoordinates([...coordinates, currentCoord]);
    };

  const handleClearCanvas=(event)=>{
    setCoordinates([]);
  };

  return (
    <main id="container" className="App-main" >
      <img src={warzone} className="img-warzone" alt="logo" />
      <ContextMenuProvider id="menu_id">
            <div>Some Content ... </div>
        </ContextMenuProvider>
        <MyAwesomeMenu />
      <canvas 
        className="App-canvas"
        ref={canvasRef}
        width={canvasWidth}
        height={canvasHeight}
        onClick={handleCanvasClick} />


       
{/* 
      <div className="button" >
        <button onClick={handleClearCanvas} > CLEAR </button>
      </div> */}
    </main>
  );

};

export default App;